.video-call-container {
  display: flex;
  flex-direction: column;
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 80%;
  margin: 0 auto;
  position: relative;
}

.active-users-counter {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #007bff;
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 14px;
}

h2 {
  font-size: 12px;
  color: #333;
  margin-bottom: 20px;
}

.find-chat-button, .end-call-button {
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin-bottom: 20px;
}

.find-chat-button {
  background-color: #007bff;
}

.find-chat-button:hover {
  background-color: #0056b3;
}

.find-chat-button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.end-call-button {
  background-color: #dc3545;
}

.end-call-button:hover {
  background-color: #c82333;
}

.waiting-text {
  color: #ff0000;
  font-size: 16px;
  margin-bottom: 20px;
}

.no-users-text {
  color: #ff0000;
  font-size: 16px;
  margin-bottom: 20px;
}

.video-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 20px;
}

.video {
  width: 45%;
  height: auto;
  border: 2px solid #333;
  border-radius: 5px;
  background-color: #000;
}

.permission-container,
.permission-denied-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f0f0;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 80%;
  max-width: 600px;
  margin: 50px auto;
  text-align: center;
}

.permission-button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin: 20px 0;
  transition: background-color 0.3s;
}

.permission-button:hover {
  background-color: #218838;
}

.permission-button:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}

.permission-info {
  margin-top: 20px;
  text-align: left;
  background-color: #e9ecef;
  padding: 15px;
  border-radius: 5px;
  width: 100%;
}

.permission-info ul {
  margin-left: 20px;
}

.permission-denied-container {
  background-color: #fff3f3;
  border: 1px solid #dc3545;
}

.permission-denied-container h2 {
  color: #dc3545;
}

.error-text {
  color: #dc3545;
  font-size: 16px;
  margin-bottom: 20px;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  padding: 10px;
  border-radius: 5px;
}

/* Add these styles for the insecure context message */
.permission-insecure-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff3cd;
  padding: 30px;
  border-radius: 10px;
  border: 1px solid #ffeeba;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 80%;
  max-width: 800px;
  margin: 50px auto;
  text-align: center;
}

.permission-insecure-container h2 {
  color: #856404;
  font-size: 24px;
  margin-bottom: 20px;
}

.permission-warning {
  background-color: #fff3cd;
  padding: 10px 15px;
  border-radius: 5px;
  border: 1px solid #ffeeba;
  margin: 10px 0;
  text-align: left;
}

.permission-warning p {
  margin: 5px 0;
}

.access-options {
  text-align: left;
  margin-top: 20px;
  width: 100%;
}

.access-options h3 {
  margin-bottom: 15px;
  color: #856404;
}

.access-options ol {
  padding-left: 20px;
}

.access-options li {
  margin-bottom: 15px;
}

.access-options code {
  background-color: #f8f9fa;
  padding: 2px 4px;
  border-radius: 3px;
  font-family: monospace;
  font-size: 90%;
}

.force-permission-button {
  background-color: #ffc107;
  color: #212529;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin: 10px 0;
  transition: background-color 0.3s;
}

.force-permission-button:hover {
  background-color: #e0a800;
}
